.collapse:not(.show) {
  display: none
}

:root {
  --tooltip-font-size: .875rem;
  --tooltip-max-width: 200px;
  --tooltip-color: #fff;
  --tooltip-background-color: #000;
  --tooltip-border-radius: .25rem;
  --tooltip-opacity: 0.9px;
  --tooltip-padding-y: 0.25rem;
  --tooltip-padding-x: 0.5rem;
  --tooltip-margin: 0;
  --tooltip-z-index: 1070px;
  --tooltip-arrow-width: .8rem;
  --tooltip-arrow-height: .4rem;
  --tooltip-arrow-color: #000
}

.tooltip {
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  text-align: start;
  text-decoration: none;
  text-shadow: none;
  text-transform: none;
  letter-spacing: normal;
  word-break: normal;
  word-spacing: normal;
  line-break: auto;
  position: absolute;
  z-index: 1070;
  display: block;
  margin: 0;
  font-size: .875rem;
  word-wrap: break-word;
  opacity: 0
}

.tooltip.show {
  opacity: 0.9
}

.tooltip .tooltip-arrow {
  position: absolute;
  display: block;
  width: .8rem;
  height: .4rem
}

.tooltip .tooltip-arrow::before {
  position: absolute;
  content: "";
  border-color: transparent;
  border-style: solid
}

.bs-tooltip-top {
  padding: .4rem 0
}

.bs-tooltip-top .tooltip-arrow {
  bottom: 0
}

.bs-tooltip-top .tooltip-arrow::before {
  top: 0;
  border-width: .4rem 0.4rem 0;
  border-top-color: #000
}

.bs-tooltip-bottom {
  padding: .4rem 0
}

.bs-tooltip-bottom .tooltip-arrow {
  top: 0
}

.bs-tooltip-bottom .tooltip-arrow::before {
  bottom: 0;
  border-width: 0 0.4rem .4rem;
  border-bottom-color: #000
}

.bs-tooltip-left {
  padding: 0 .4rem
}

.bs-tooltip-left .tooltip-arrow {
  right: 0;
  width: .4rem;
  height: .8rem
}

.bs-tooltip-left .tooltip-arrow::before {
  left: 0;
  border-width: 0.4rem 0 0.4rem .4rem;
  border-left-color: #000
}

.bs-tooltip-right {
  padding: 0 .4rem
}

.bs-tooltip-right .tooltip-arrow {
  left: 0;
  width: .4rem;
  height: .8rem
}

.bs-tooltip-right .tooltip-arrow::before {
  right: 0;
  border-width: 0.4rem .4rem 0.4rem 0;
  border-right-color: #000
}

.bs-tooltip-auto[x-placement^="top"] .bs-tooltip-top {
  padding: .4rem 0
}

.bs-tooltip-auto[x-placement^="top"] .bs-tooltip-top .tooltip-arrow {
  bottom: 0
}

.bs-tooltip-auto[x-placement^="top"] .bs-tooltip-top .tooltip-arrow::before {
  top: 0;
  border-width: .4rem 0.4rem 0;
  border-top-color: #000
}

.bs-tooltip-auto[x-placement^="bottom"] .bs-tooltip-bottom {
  padding: .4rem 0
}

.bs-tooltip-auto[x-placement^="bottom"] .bs-tooltip-bottom .tooltip-arrow {
  top: 0
}

.bs-tooltip-auto[x-placement^="bottom"] .bs-tooltip-bottom .tooltip-arrow::before {
  bottom: 0;
  border-width: 0 0.4rem .4rem;
  border-bottom-color: #000
}

.bs-tooltip-auto[x-placement^="left"] .bs-tooltip-left {
  padding: 0 .4rem
}

.bs-tooltip-auto[x-placement^="left"] .bs-tooltip-left .tooltip-arrow {
  right: 0;
  width: .4rem;
  height: .8rem
}

.bs-tooltip-auto[x-placement^="left"] .bs-tooltip-left .tooltip-arrow::before {
  left: 0;
  border-width: 0.4rem 0 0.4rem .4rem;
  border-left-color: #000
}

.bs-tooltip-auto[x-placement^="right"] .bs-tooltip-right {
  padding: 0 .4rem
}

.bs-tooltip-auto[x-placement^="right"] .bs-tooltip-right .tooltip-arrow {
  left: 0;
  width: .4rem;
  height: .8rem
}

.bs-tooltip-auto[x-placement^="right"] .bs-tooltip-right .tooltip-arrow::before {
  right: 0;
  border-width: 0.4rem .4rem 0.4rem 0;
  border-right-color: #000
}

.tooltip-inner {
  max-width: 200px;
  padding: 0.25rem 0.5rem;
  color: #fff;
  text-align: center;
  background-color: #000;
  border-radius: .25rem
}